body {
  font-family: sans-serif;
}

.App {
  height: 100vh;
  width: 100%;
  background: url(./images/bg1.jpeg) no-repeat center/cover;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.input {
  background-color: #fff;
  font-size: 25px;
  padding: 10px 35px;
  border: none;
  border-radius: 8px;
  outline: none;
  color: #111;
  box-shadow: 0px 3px 10px #00000050;
  transition: 200ms;
}

.input:focus {
  box-shadow: 0px 8px 15px #00000030;
}

.input::placeholder {
  color: #00000052;
  text-align: center;
}

.result {
  margin: 40px 0 0;
  min-height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.data {
  font-size: 25px;
  margin: 0 15px;
  line-height: 30px;
  height: 30px;
}

.data img {
  height: 100%;
}


@media only screen and (max-width: 325px){

  form {
    display: flex;
    justify-content: center;
  }
  .input {
    width: calc(100% - 20px);
    box-sizing: border-box;
  }

  .result {
    flex-direction: column;
    min-height: 160px;
  }
  .result .data {
    margin: 5px 0;
  }
}